import React from "react";

import {
  AnimatePresence,
  AnimateSharedLayout,
  motion,
  MotionConfig,
} from "framer-motion";

import useGame from "./hooks/useGame";
import { gameStates } from "./app/constants";
import Board from "./features/board/Board";
import PlayerIndicator from "./features/board/PlayerIndicator";
import StartScreen from "./features/startScreen/StartScreen";
import Modal from "./features/modal/Modal";

const App = () => {
  const { gameState } = useGame();
  return (
    <motion.main className="w-full h-screen flex flex-col-reverse lg:flex-row items-center justify-center">
      <MotionConfig
        transition={{
          type: "spring",
          mass: 10,
          stiffness: 800,
          damping: 200,
          restDelta: 0.001,
        }}
      >
        <AnimateSharedLayout>
          <AnimatePresence exitBeforeEnter>
            {gameState === gameStates.START ? (
              <StartScreen key={1} />
            ) : (
              <motion.div layout>
                <Board />
              </motion.div>
            )}
            {gameState === gameStates.CROSS_WINS && (
              <Modal key={"cross-modal"}>
                üéâ <span className="text-red-500">Cross</span> Wins!
              </Modal>
            )}
            {gameState === gameStates.ZERO_WINS && (
              <Modal key={"zero-modal"}>
                üéâ <span className="text-blue-500">Zero</span> Wins!
              </Modal>
            )}
            {gameState === gameStates.DRAW && (
              <Modal key={"draw-modal"}>üôÖ‚Äç‚ôÇÔ∏è Draw!</Modal>
            )}
          </AnimatePresence>
        </AnimateSharedLayout>
      </MotionConfig>

      {/* <PlayerIndicator /> */}
    </motion.main>
  );
};

export default App;
